
import { defineComponent, computed, ref } from "vue";
import { ElIcon } from "element-plus";
import { Back, Right } from "@element-plus/icons";
export default defineComponent({
  name: "Slider",
  components: { ElIcon, Back, Right },
  props: {
    // 設定高度
    height: {
      type: String,
      default: "100%",
    },
    // 是否禁用下一頁按鈕
    disableNext: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    // 當前頁碼
    const num = ref(0);
    // 是否到達最後一頁
    const isEnd = ref(false);
    // 換頁動畫：計算頁位置
    const setSlide = computed(() => {
      return {
        transform: `translateX(-${100 * num.value}%)`,
      };
    });
    // $refs
    const sliderContent = ref(null as any);
    return {
      props,
      // 當前頁碼
      num,
      // 是否到達最後一頁
      isEnd,
      // 換頁動畫：計算頁位置
      setSlide,
      // $refs
      sliderContent,
    };
  },
  methods: {
    /**
     * 換頁
     *
     * @param {number} step - 上一頁(1)或下一頁(-1)
     */
    handleSlide(step: number) {
      // 進入下一頁時，預設不能再進入下一頁
      if (step === 1 && this.disableNext) {
        return;
      }

      // 離開 submit 狀態
      if (step === -1 && this.isEnd) {
        this.isEnd = false;
        return;
      }

      // submit 狀態
      if (this.isEnd) {
        this.$emit("slider:submit");
        this.reset();
        return;
      }

      // 到首頁
      if (this.num + step < 0) {
        this.$emit("slider", "start");
        return;
      }

      // 到末頁
      if (this.num + step === this.sliderContent.children.length) {
        this.$emit("slider", "end");
        this.isEnd = true;
        return;
      }

      // 正常切換頁
      if (!this.isEnd) {
        this.num += step;
      }
      this.$emit("slider", this.num);
      this.isEnd = false;
    },

    /**
     * 重設 slider
     */
    reset() {
      this.isEnd = false;
      this.num = 0;
    },
  },
});
