
import { Plus, Delete, Setting, View } from "@element-plus/icons";
import { defineComponent, ref, computed } from "vue";
import { ElIcon, ElMessageBox } from "element-plus";

export default defineComponent({
  name: "Item",
  components: { ElIcon, Delete, Plus, Setting, View },
  props: {
    id: Number,
    url: {
      type: String,
      default: "",
    },
    title: {
      type: String,
      default: "",
    },
    lock: {
      type: Boolean,
      default: false,
    },
    complete: {
      type: Boolean,
      default: false,
    },
    color: Boolean,
    sort: Number,
  },
  setup(props) {
    let isEdit = ref(false);
    // 設定字體大小
    const titleFontSize = computed(() => {
      const len = props.title.length;
      if (len < 10) {
        return {
          fontSize: "48px",
        };
      }
      if (len < 20) {
        return {
          fontSize: "32px",
        };
      }
      return {
        fontSize: "24px",
      };
    });
    return {
      props,
      isEdit,
      titleFontSize,
    };
  },
  methods: {
    /**
     * 處理刪除動作
     *
     * @param {object} evt - event
     * @param {number} id - id
     */
    handleDelete(evt: any, id: number) {
      ElMessageBox.confirm("Are you sure you want to delete this?", {
        confirmButtonText: "Delete",
      })
        .then(() => {
          this.$emit("item:delete", id);
        })
        .catch(() => {
          // catch error
        });
    },
  },
});
